<template>
  <div class="col-md-12 p-0" id="projectdtl">
    <loader :is-visible="isLoading"></loader>
    <section id="divMainListData" class="main-content no-left-bar">
      <div class="user-guide-custom d-none" id="User_Guide_View_Billing_Setup" v-html="userGuideContent"></div>
      <div class="container-fluid padding-t_10">
        <div class="right-content">
          <div class="col-md-12 p-0">
            <div class="theme-primary partition p-actions-expand">
              <span class="p-name text-white">{{ $t('SetupDetail') }}</span>
              <span class="p-actions float-right">
                <a
                  @click="backToListUrl"
                  id="ancbacktolist"
                  class="p-action-btn text-white"
                  :title="$t('BacktoList')"
                >
                  <i class="fa fa-angle-double-left"></i><br />{{ $t('BacktoList') }}
                </a>
              </span>
              <span class="collapse-head-btns">
                <a href="javascript:;" class="collapse-btns">
                  <i class="fa fa-angle-up text-white"></i>
                </a>
              </span>
            </div>
          </div>
          <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 searchpanel tablehead search float-left p-0">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 Search-panel float-left py-2 p-0">
              <div class="col-sm-12 col-md-12 col-lg-6 col-xl-6 float-left">
                <div class="row mt-0">
                  <div class="col-6 col-sm-6 col-md-6 col-lg-6 col-xl-6 float-left pr-1 pl-0">
                    <div class="form-group">
                      <input
                        type="text"
                        id="SetupName"
                        name="TemplateName"
                        class="form-control"
                        :value="templateName"
                        readonly
                      />
                    </div>
                  </div>
                  <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left d-none">
                    <div class="form-group">
                      <div class="search-btm-btn">
                        <a href="javascript:;" class="btn btn-success">
                          <i class="fa fa-search"></i>
                        </a>
                        <a href="javascript:;" class="btn btn-danger">
                          <i class="fa fa-refresh"></i>
                        </a>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="tab-pane fade show active border-0 p-0" id="BillingSetup" role="tabpanel" aria-labelledby="BillingSetup-tab">
            <div class="listing">
              <div class="table-responsive">
                <table id="tblBillingSetup" class="table table-bordered dt-responsive nowrap mt-0" style="width:100%;">
                  <thead class="thead-bg">
                    <tr>
                      <th><span>{{ $t('ImportedField') }}</span></th>
                      <th><span>{{ $t('DataType') }}</span></th>
                      <th><span>{{ $t('fieldName') }}</span></th>
                      <th><span>{{ $t('CustomColumnName') }}</span></th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item, index) in items" :key="item.setUpColumnId || index">
                      <td>
                        <span>{{ item.importedColumn }}</span>
                      </td>
                      <td>
                        <span>{{ item.dataType }}</span>
                      </td>
                      <td>
                        <span>{{ item.importedColumn }}</span>
                      </td>
                      <td>
                        <span>{{ item.customName }}</span>
                      </td>
                      <input type="hidden" :value="item.setupId" id="hdnSetupId" />
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import { locateBillingService } from '../../services/locateBillingService'

export default {
  name: 'SetupDetail',
  data() {
    return {
      isLoading: false,
      templateName: '',
      items: [],
      userGuideContent: ''
    }
  },
  created() {
    this.loadData()
  },
  methods: {
    loadData() {
      this.isLoading = true
        var params = `setupId=${this.$route.params.setupId}&isEditable=0`;
      locateBillingService.GetSetupUpdateRecords(params).then((response) => {
        this.isLoading = false;
        if (response.data) {
          this.items = response.data.setupDetails || []
          this.templateName = response.data.templateName || ''
        }
      }).catch((error) => {
        this.isLoading = false
        console.error('Error loading setup details:', error)
      })
    },
     backToListUrl(){
      var vm =this;
      vm.$router.push({ name: 'ImportFormatListing' });
    },
  },
}
</script>

<style scoped>
/* Add any component-specific styles here */
</style>